.apt-card{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.apt-card-div{
    padding: 1% ;
}

.card{
    position: relative;
    padding: 1%;
    flex-grow: 1;
    width: fit-content;
}

.card-img{
    width: 340px;
    height: 340px;
    border-radius: 10px;
    object-fit: cover;
    margin-bottom: 20px; 
}

.card-title{
    position: absolute;
    bottom: 10px;
    width: 95%;
    padding-bottom: 30px;
    padding-left: 10px;
    font-size: 18px;
    border-radius: 10px;
    color: white;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(0, 0, 0, 0.5) 100%);
}

@media only screen and (max-width: 700px) {
    .card-img{
        border-radius: 20px;
        padding: 10px;
        width: 335px;
        height: 255px;
    }
    .card-title{
        width: 60%;
        padding-right: 108px;
        left: 13px;
        bottom: 20px;
    }
}